






































































{ Query, read_at } = require "~plugins/memory-record"
{ computed, mounted } = require "~plugins/book"

module.exports =
  default:
    data: ->
      read_at: read_at

    mounted: ->
      @$store.dispatch "sow/story", @book_id
      .then =>
        mounted.call @

    methods:
      focus: (idx)->
        { name, params, query } = @$route
        params = { params..., idx }
        @$router.replace { name, params, query }
        
      anker: (book_id, a)->
        back = [a[0], @mode, @pages ].join(",")
        @$router.push
          path: "../#{@book_id}/anker"
          query: { a, back }

      page_add: (id)->
        page_idxs = [@page_idxs[0], id]
        @book = { @part_id, page_idxs }

      part_prev: ->
        @book =
          part_id: @part_prev_id ? @part_id
          page_idxs: [0]

      part_next: ->
        @book =
          part_id: @part_next_id ? @part_id
          page_idxs: [0]

    computed: {
      computed...
      part_prev_id: ->
        if @part && @book
          ids = @book.parts.pluck('id')
          idx = ids.indexOf @part_id
          ids[idx - 1]

      part_next_id: ->
        if @part && @book
          ids = @book.parts.pluck('id')
          idx = ids.indexOf @part_id
          ids[idx + 1]


      page_here_id: ->
        [..., last] = @page_idxs
        last

      page_next_id: ->
        if @page_here_id? && @page_here_id + 1 < @chats_here.length
          @page_here_id + 1

      now: ->
        @read_at["book.#{@book_id}"]
        Query.chats.now(@hide_potof_ids)

      chats: ->
        @now[@mode]

      chats_here: ->
        @chats(@part_id)

      chats_pages: ->
        @page_idxs.map (page)=> @chats_here[page]
    }




